@import '../../config.scss';

.productItem {
  display: grid;
  grid-template-columns: repeat(2, 10rem);
  grid-row-gap: 0.5rem;
  transition: all 0.3s;
  align-items: center;

  @include respond(tab-port) {
    grid-template-columns: repeat(2, 10rem);
  }

  @include respond(phone) {
    grid-template-columns: repeat(2, 11rem);
  }

  .image {
    width: 20rem;
    height: 25rem;
    grid-row: 1 / 2;
    grid-column: 1 / -1;
    object-fit: cover;
    cursor: pointer;

    @include respond(tab-port) {
      width: 20rem;
      height: 20rem;
    }

    @include respond(phone) {
      width: 22rem;
      height: 22rem;
    }
  }

  .productName {
    grid-column: 1 / 3;
    cursor: pointer;
  }

  .productPrice {
    color: secondaryColorDark;
  }

  .icons {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    svg {
      cursor: pointer;
      width: 2rem;
      height: 2rem;
      margin: 0 0.5rem;
      fill: rgb(210, 210, 210);
      color: rgb(210, 210, 210);
      transition: all 0.3s;

      @include respond(tab-port) {
        width: 2.2rem;
        height: 2.2rem;
        margin: 0 0.6rem;
      }

      @include respond(phone) {
        width: 2.4rem;
        height: 2.4rem;
        margin: 0 0.7rem;
      }

      &:hover {
        fill: secondaryColor;
        color: secondaryColor;
      }
    }

    .onWishlist {
      fill: secondaryColorDark;

      &:hover {
        fill: secondaryColorDark;
      }
    }
  }
}

.productItem:hover {
  transform: scale(1.02);
}
